import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/self-operated-goods',
      name: 'home',
      component: () => import('@/home'),
      children: [
        {
          path: '/self-operated-goods',
          name: 'self-operated-goods',
          meta: {
            title: '首页'
          },
          component: () => import('@/home/self-operated-goods')
        },
        {
          path: '/group-buy-doctor',
          name: 'group-buy-doctor',
          meta: {
            title: '首页'
          },
          component: () => import('@/home/group-buy-doctor')
        },
        {
          path: '/super-package',
          name: 'super-package',
          meta: {
            title: '首页'
          },
          component: () => import('@/home/super-package')
        }
      ]
    },
    {
      path: '/my',
      name: 'my',
      meta: {
        title: '我的'
      },
      component: () => import('@/my')
    }
  ]
})



